@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --background-color: rgb(2, 6, 13);
  --background-color-dark: rgb(49, 96, 166);
  --primary-text-color: rgb(148, 102, 234);
  --secondary-text-color: rgb(38, 174, 28);
  --img-background-color: rgba(23, 23, 23, 0.509);
  --navbar-text-color: rgb(190, 190, 190);
  --navbar-text-color-dark: rgb(130, 130, 130);
}

* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  margin: 0;
  padding: 9;
  box-sizing: border-box;
  font-family: "PT Sans", sans-serif;
}

.home-page {
    margin-top: 8vh;
    padding: 40px 180px;
    height: 92vh;
    width: 100vw;
    background: var(--background-color);
    display: flex;
    justify-content: center;
  }
  
  .text-layout {
    width: 100%;
    margin-right: -180px;
    z-index: 3;
  }
  
  .text-layout h1 {
    color: var(--primary-text-color);
    font-size: 6rem;
    margin-bottom: 3rem;
    margin-right: 10rem;
  }
  
  .text-layout h4 {
    color: var(--secondary-text-color);
    font-size: 3rem;
  }
  
  .img-background {
    width: 550px;
    border-radius: 35px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: var(--img-background-color);
  }
  
  .img {
    width: 450px;
    height: 100%;
  }
  
  #titleLine1::after {
    content: "\a";
    white-space: pre;
  }
  
  @media only screen and (max-width: 1250px) {
    .home-page {
      padding: 40px 100px;
    }
  }
  
  @media only screen and (max-width: 1107px) {
    .home-page {
      padding: 50px 200px;
      height: auto;
      flex-direction: column;
    }
  
    .text-layout {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
      margin-bottom: 50px;
      margin-right: 0px;
    }
  
    .text-layout h1 {
      font-size: 5rem;
      margin-bottom: 2rem;
    }
  
    .text-layout h4 {
      font-size: 2.5rem;
    }
  
    .img-background {
      width: 100%;
      justify-content: center;
    }
  }
  
  @media only screen and (max-width: 855px) {
    .home-page {
      padding: 30px 160px;
    }
  
    .text-layout h1 {
      font-size: 5rem;
      margin-bottom: 2rem;
    }
  
    .text-layout h4 {
      font-size: 2.5rem;
    }
  
    .img-background {
      width: 100%;
      justify-content: center;
    }
  
    .img {
      width: 300px;
      height: 100%;
    }
  }
  
  @media only screen and (max-width: 772px) {
    .home-page {
      padding: 30px 130px;
    }
  
    .text-layout h1 {
      font-size: 4rem;
      margin-bottom: 1rem;
    }
  
    .text-layout h4 {
      font-size: 2rem;
    }
  
    .img-background {
      width: 100%;
      justify-content: center;
    }
  
    .img {
      width: 300px;
      height: 100%;
    }
  }
  
  @media only screen and (max-width: 624px) {
    .home-page {
      padding: 30px 40px;
    }
  
    .text-layout h1 {
      font-size: 3rem;
      margin-bottom: 1rem;
    }
  
    .text-layout h4 {
      font-size: 1.5rem;
    }
  
    .img {
      width: 260px;
    }
  }
  
.navbar {
    height: 8vh;
    width: 100vw;
    background: var(--background-color);
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
  }
  
  .home-title-logo {
    height: 100%;
    font-size: 2rem;
    width: 15rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: var(--navbar-text-color);
    cursor: pointer;
    transition: 0.2s ease-in-out;
    padding: 0 1rem;
  }
  
  .home-title-logo:hover {
    transform: scale(1.2);
    background: var(--primary-text-color);
    color: white;
  }
  
  .nav-items {
    width: 60rem;
    height: 100%;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 1.5rem;
  }
  
  .nav-item {
    height: 100%;
    margin-left: 1.5rem;
    padding: 0rem 1.5rem;
    transition: 0.2s ease-in-out;
  }
  
  .nav-item:hover {
    transform: scale(1.2);
    background: var(--primary-text-color);
  }
  
  .nav-item:hover a {
    color: white;
  }
  
  .nav-item a {
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    color: var(--navbar-text-color);
    text-decoration: none;
  }
  
  .sidebar-toggle {
    width: 40vw;
    display: flex;
    justify-content: flex-end;
    margin-right: 10px;
  }
  
  .sidebar-toggle-logo {
    color: var(--navbar-text-color);
    text-decoration: none;
    font-size: 2.5rem;
    cursor: pointer;
  }
  
  .sidebar {
    background: var(--background-color);
    height: 92vh;
    width: 16rem;
    position: fixed;
    top: 8vh;
    left: -100%;
    transition: 0.3s all ease-in-out;
    z-index: 10;
  }
  
  .sidebar.active {
    left: 0;
    transition: 0.5s all ease-out;
  }
  
  .sidebar-items {
    padding: 10px 10px;
    text-decoration: none;
    font-size: 1.2rem;
    flex-direction: column;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s all ease-out;
  }
  
  .sidebar-item {
    list-style: none;
    width: 100%;
    height: 70px;
  }
  
  .sidebar-item a {
    text-decoration: none;
    color: var(--navbar-text-color);
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    border-radius: 8px;
    cursor: pointer;
    transition: 0.2s all ease-out;
  }
  
  .sidebar-item a:hover {
    background: var(--primary-text-color);
  }
  
  @media only screen and (max-width: 900px) {
    .home-title-logo:hover {
      transform: none;
      background: none;
      color: var(--navbar-text-color);
    }
  }
  
.techstack-page {
    padding: 30px 180px;
    width: 100vw;
    height: 92vh;
    background: var(--background-color);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }
  
  .techstack-page-title {
    color: var(--primary-text-color);
    font-size: 3.5rem;
  }
  
  .line-break-details {
    margin: 20px 0 30px 0;
    background: var(--primary-text-color);
    height: 2px;
    width: 75vw;
  }
  
  .tech-stack-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-row-gap: 1rem;
    justify-content: space-around;
  }
  
  .stack {
    background: var(--img-background-color);
    width: 300px;
    height: auto;
    padding: 15px 30px;
    border-radius: 20px;
  }
  
  .tech-heading {
    color: var(--primary-text-color);
    font-size: 32px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 12px;
  }
  
  .tech-heading-logo {
    margin-right: 12px;
  }
  
  .technology {
    color: var(--secondary-text-color);
    font-size: 24px;
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: flex-start;
  }
  
  .technology-list {
    padding-left: 0px;
  }
  
  .technology div {
    margin-right: 1.1rem;
  }
  
  @media only screen and (max-width: 1250px) {
    .techstack-page {
      padding: 30px 80px;
    }
  }
  
  @media only screen and (max-width: 1107px) {
    .techstack-page {
      padding: 30px 40px;
    }
  }
  
  @media only screen and (max-width: 855px) {
    .techstack-page {
      padding: 30px 100px;
      height: auto;
    }
  
    .techstack-page-title {
      font-size: 3rem;
    }
  
    .tech-stack-container {
      grid-template-columns: repeat(2, auto);
    }
  
    .stack {
      width: 240px;
    }
  
    .tech-heading {
      font-size: 26px;
    }
  
    .technology {
      font-size: 20px;
    }
  }
  
  @media only screen and (max-width: 772px) {
    .techstack-page {
      padding: 30px 60px;
    }
  
    .tech-stack-container {
      grid-template-columns: repeat(2, auto);
    }
  
    .tech-heading {
      font-size: 26px;
    }
  
    .stack {
      width: 260px;
    }
  }
  
  @media only screen and (max-width: 624px) {
    .techstack-page {
      padding: 30px 30px;
    }
  
    .techstack-page-title {
      font-size: 2.2rem;
    }
  
    .tech-stack-container {
      grid-template-columns: repeat(1, auto);
    }
  
    .technology {
      font-size: 20px;
    }
  }
  
.contact-page {
    padding: 30px 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    margin-top: 8vh;
  }
  
  .contact-form {
    background: var(--primary-text-color);
    padding: 32px 48px;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
  }
  
  .contact-form-title {
    color: white;
    font-size: 2.5rem;
  }
  
  .input {
    margin-top: 1.5rem;
    height: 2.5rem;
    display: block;
    width: 100%;
    padding: 0.75rem;
    box-sizing: border-box;
    border-radius: 10px;
    border: 3px solid #dddddd;
    outline: none;
    background: #eeeeee;
    transition: background 0.2s, border-color 0.2s;
  }
  
  .input:focus {
    border-color: var(--secondary-text-color);
    background: #fff;
  }
  
  .input-message {
    height: 10rem;
  }
  
  .btn {
    margin-top: 1.5rem;
    height: 3.5rem;
    display: block;
    width: 100%;
    border-radius: 10px;
    border: 2px solid #dddddd;
    color: var(--navbar-text-color-dark);
    font-size: 1.5rem;
    cursor: pointer;
    transition: 0.2s all ease-in-out;
  }
  
  .btn:hover {
    color: white;
    border: 2px solid #dddddd;
    background: var(--secondary-text-color);
    transform: scale(1.05);
  }
  
  .social-media-section {
    padding: 32px 48px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 50%;
    height: 30rem;
  }
  
  .upper,
  .lower {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
  }
  
  .icons {
    margin-top: 16px;
    width: 80%;
    display: flex;
    justify-content: space-around;
  }
  
  .icon {
    font-size: 48px;
    color: var(--primary-text-color);
    cursor: pointer;
    transition: 0.2s all ease-in-out;
  }
  
  .icon:hover {
    color: var(--secondary-text-color);
  }
  
  .social-media-section-heading {
    color: var(--primary-text-color);
    font-size: 3rem;
  }
  
  .social-media-section h3 {
    color: var(--secondary-text-color);
    text-decoration: underline;
    font-size: 2rem;
    cursor: pointer;
  }
  
  @media only screen and (max-width: 1250px) {
    .contact-page {
      padding: 30px 110px;
    }
  }
  
  @media only screen and (max-width: 1107px) {
    .contact-page {
      padding: 30px 70px;
    }
  }
  
  @media only screen and (max-width: 855px) {
    .contact-page {
      padding: 30px 70px;
      flex-direction: column;
      height: auto;
    }
    .contact-form {
      width: 90%;
    }
    .social-media-section {
      justify-content: space-around;
      width: 90%;
      height: 400px;
    }
  }
  
  @media only screen and (max-width: 772px) {
    .contact-page {
      padding: 30px 50px;
    }
    .contact-form {
      padding: 16px 24px;
      width: 95%;
    }
    .social-media-section {
      padding: 16px 24px;
      width: 100%;
      height: 400px;
    }
    .icons {
      margin-top: 16px;
      width: 100%;
    }
  }
  
  @media only screen and (max-width: 624px) {
    .contact-page {
      padding: 10px 20px;
    }
  
    .social-media-section {
      width: 100%;
      height: 320px;
    }
    .social-media-section-heading {
      font-size: 2rem;
    }
  
    .social-media-section h3 {
      color: var(--secondary-text-color);
      text-decoration: underline;
      font-size: 1.5rem;
      cursor: pointer;
    }
  
    .icon {
      font-size: 40px;
    }
  }
  
.project_section {
    padding: 15px 180px 15px 180px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    background: var(--background-color);
    height: 92vh;
  }
  
  .line-break-project {
    margin: 10px 0 20px 0;
    background: var(--primary-text-color);
    height: 2px;
    width: 75vw;
  }
  
  .page-title {
    color: var(--primary-text-color);
    font-size: 3rem;
  }
  
  .projects {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 20px;
    justify-content: center;
    align-items: center;
  }
  
  .project {
    padding: 8px 24px;
    width: 360px;
    height: 230px;
    background: var(--img-background-color);
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    border-radius: 16px;
    transition: 0.3s all ease-in-out;
  }
  
  .project:hover {
    transform: scale(1.05);
  }
  
  .project-title {
    color: var(--primary-text-color);
    font-size: 30px;
  }
  
  .project-description {
    color: var(--navbar-text-color-dark);
    font-size: 16px;
  }
  
  .project-code {
    padding: 10px 26px;
    border-radius: 6px;
    background: var(--secondary-text-color);
    color: white;
    text-decoration: none;
    transition: 0.3s all ease-in-out;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .project-code:hover {
    background: var(--primary-text-color);
    color: white;
    transform: scale(1.1);
  }
  
  @media only screen and (max-width: 1250px) {
    .project-section {
      padding: 30px 150px;
    }
  }
  
  @media only screen and (max-width: 1107px) {
    .project_section {
      padding: 15px 150px 15px 150px;
      height: auto;
    }
    .projects {
      grid-template-columns: repeat(2, auto);
    }
  }
  
  @media only screen and (max-width: 855px) {
    .project_section {
      padding: 30px 120px;
      height: auto;
    }
  }
  
  @media only screen and (max-width: 772px) {
    .project_section {
      padding: 20px 80px;
    }
  
    .project {
      padding: 8px 16px;
      width: 300px;
      height: 200px;
    }
  
    .project-title {
      font-size: 24px;
    }
  
    .project-description {
      font-size: 14px;
    }
  
    .project-code {
      padding: 4px 16px;
      font-size: 1.1rem;
    }
  }
  
  @media only screen and (max-width: 624px) {
    .project-section {
      padding: 20px 30px;
      height: auto;
    }
  
    .projects {
      grid-template-columns: repeat(1, auto);
    }
  
    .project {
      width: 320px;
      height: auto;
    }
  
    .project-title {
      font-size: 30px;
    }
  
    .project-description {
      font-size: 16px;
      margin: 16px 0;
    }
  
    .project-code {
      padding: 10px 26px;
      font-size: 1.5rem;
    }
  }
  
