.contact-page {
    padding: 30px 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    margin-top: 8vh;
  }
  
  .contact-form {
    background: var(--primary-text-color);
    padding: 32px 48px;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
  }
  
  .contact-form-title {
    color: white;
    font-size: 2.5rem;
  }
  
  .input {
    margin-top: 1.5rem;
    height: 2.5rem;
    display: block;
    width: 100%;
    padding: 0.75rem;
    box-sizing: border-box;
    border-radius: 10px;
    border: 3px solid #dddddd;
    outline: none;
    background: #eeeeee;
    transition: background 0.2s, border-color 0.2s;
  }
  
  .input:focus {
    border-color: var(--secondary-text-color);
    background: #fff;
  }
  
  .input-message {
    height: 10rem;
  }
  
  .btn {
    margin-top: 1.5rem;
    height: 3.5rem;
    display: block;
    width: 100%;
    border-radius: 10px;
    border: 2px solid #dddddd;
    color: var(--navbar-text-color-dark);
    font-size: 1.5rem;
    cursor: pointer;
    transition: 0.2s all ease-in-out;
  }
  
  .btn:hover {
    color: white;
    border: 2px solid #dddddd;
    background: var(--secondary-text-color);
    transform: scale(1.05);
  }
  
  .social-media-section {
    padding: 32px 48px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 50%;
    height: 30rem;
  }
  
  .upper,
  .lower {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
  }
  
  .icons {
    margin-top: 16px;
    width: 80%;
    display: flex;
    justify-content: space-around;
  }
  
  .icon {
    font-size: 48px;
    color: var(--primary-text-color);
    cursor: pointer;
    transition: 0.2s all ease-in-out;
  }
  
  .icon:hover {
    color: var(--secondary-text-color);
  }
  
  .social-media-section-heading {
    color: var(--primary-text-color);
    font-size: 3rem;
  }
  
  .social-media-section h3 {
    color: var(--secondary-text-color);
    text-decoration: underline;
    font-size: 2rem;
    cursor: pointer;
  }
  
  @media only screen and (max-width: 1250px) {
    .contact-page {
      padding: 30px 110px;
    }
  }
  
  @media only screen and (max-width: 1107px) {
    .contact-page {
      padding: 30px 70px;
    }
  }
  
  @media only screen and (max-width: 855px) {
    .contact-page {
      padding: 30px 70px;
      flex-direction: column;
      height: auto;
    }
    .contact-form {
      width: 90%;
    }
    .social-media-section {
      justify-content: space-around;
      width: 90%;
      height: 400px;
    }
  }
  
  @media only screen and (max-width: 772px) {
    .contact-page {
      padding: 30px 50px;
    }
    .contact-form {
      padding: 16px 24px;
      width: 95%;
    }
    .social-media-section {
      padding: 16px 24px;
      width: 100%;
      height: 400px;
    }
    .icons {
      margin-top: 16px;
      width: 100%;
    }
  }
  
  @media only screen and (max-width: 624px) {
    .contact-page {
      padding: 10px 20px;
    }
  
    .social-media-section {
      width: 100%;
      height: 320px;
    }
    .social-media-section-heading {
      font-size: 2rem;
    }
  
    .social-media-section h3 {
      color: var(--secondary-text-color);
      text-decoration: underline;
      font-size: 1.5rem;
      cursor: pointer;
    }
  
    .icon {
      font-size: 40px;
    }
  }
  